.homepage-container {
  background: linear-gradient(1deg, #ffffff 0%, #eae6ff 80%);

  .homepage-wrapper {
    max-width: 1140px;
    max-height: 575px;
    padding: 112px 15px 0px 15px;
    margin: 0 61px;

    .homepage {
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
      align-items: center;
    }

    .homepage-title {
      inline-size: 100%;
      // inline-size: 90%;
      margin-bottom: 0.5rem;
      line-height: 60px;
      font-size: 48px;
      font-family: "Charlie-Display-Semibold";
      color: #091e42;
      text-align: center;
    }

    .homepage-paragraph {
      color: #091e42;
      line-height: 30px;
      text-align: center;
      inline-size: 100%;
      overflow-wrap: break-word;
      font-size: 20px;
      font-family: "Charlie-Display-Semibold";
      margin-bottom: 24px;
    }

    .demo-btn {
      border: 1px solid transparent;
      padding: 0.375rem 0.75rem;
      font-size: 1.25rem;
      border-radius: 6px;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      color: #fff;
      background-color: #0065ff;
      display: block;
      width: 100%;
      cursor: pointer;
      margin-top: 48px;
      margin-bottom: 20px;
      font-family: "Charlie-Display-Semibold";
    }

    .demo-btn:hover {
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
      display: block;
      text-decoration: none;
      color: #fff;
      background-color: #0056d9;
      border-color: #0051cc;

    }

    .homepage-hero {
      width: 300px;
      min-height: 200px;
      min-width: 200px;
    }

    .app-info {
      margin-top: 5rem;
      text-align: center;

      .introduction-header {
      font-family: "Charlie-Display-Semibold";
        font-size: 2.25rem;
        line-height: 1.333;
        margin-bottom: 0.5rem;
      }

      .introduction {
      font-family: "Charlie-Display-Semibold";
        font-size: rem(24px);
        margin-bottom: 5rem;
        line-height: 1.3;
      }

      .demo-img-container {
        margin: auto;
        max-width: 900px;
        width: 100%;
        margin-bottom: 4rem;
        box-shadow: 0px 0px 16px 0px rgba(130, 130, 130, 1);

        img {
          object-fit: contain;
          width: 100%;
          height: 100%;
          border-radius: 4px;
        }
      }
    }

    @media screen and (min-width: 1000px) {
      .homepage-hero {
        width: 710px;
      }

      .homepage {
        flex-direction: row;
      }

      .homepage-paragraph {
        text-align: left;
        inline-size: 85%;
      }

      .homepage-title {
        text-align: left;
        inline-size: 90%;
      }

      .demo-btn {
        width: 30%;
      }
    }
  }
}

.home-page {
  position: relative;
  width: 100%;
  height: fit-content;
  a {
      text-decoration: none;
      color: #fff;
      font-family: "Charlie-Display-Semibold";
  }

  .header {
      position: absolute;
      top: 0;
      height: 60px;
      width: 100%;
      background-color: #fafbfc;
      padding-inline-start: 1rem;

      .logo {
          font-size: 2rem;
      font-family: "Charlie-Display-Semibold";
          margin-inline-end: 1rem;
          span {
              color: #0065ff;
          }
          span:last-of-type {
              color: #253858;
              margin-top: 0.1rem;
          }
      }

      .login-btn {
          cursor: pointer;
          color: white;
          background-color: #0065ff;
      font-family: "Charlie-Display-Semibold";
          font-size: 1.2rem;
          height: 100%;
          padding: 0.5rem;
          width: 200px;
          text-align: center;
          &:hover {
              &:hover {
                  filter: none;
                  background-color: rgb(7, 71, 166);
              }
          }
      }
  }

  section:nth-of-type(2) {
      position: relative;
      top: 60px;
      background-image: linear-gradient(60deg, rgb(82, 67, 170), rgb(237, 80, 180));

      .wave {
          position: absolute;
          bottom: 0;
          width: 100%;
          color: #fafbfc;
      }

      .demo-invite {
          padding: 2rem;
          z-index: 100;

          color: #fff;
          .img1 {
              margin-top: 5rem;
              width: 580px;
          }

          h1 {
              margin-top: 0rem;
      font-family: "Charlie-Display-Semibold";
              font-size: rem(48px);
              line-height: 1.25;
              width: 480px;
          }

          p {
              font-size: 1.5rem;
          }

          .btn-demo {
              @include btn-add-save($type: "save");
      font-family: "Charlie-Display-Semibold";
              padding: 0.5rem 3rem;
              box-shadow: outset 0px 0px 1px 1px rgb(0 0 0 / 10%);
              font-size: 2rem;
              background-color: #0065ff;
              &:hover {
                  filter: none;
                  background-color: rgb(7, 71, 166);
              }
          }
      }
      // border: 1px solid gold;
  }
}

@media (max-width: 768px) {
  .home-page {
      .demo-invite {
          & * {
              width: 100% !important;
          }
          width: 100% !important;
          padding: 1rem !important;
          flex-direction: column;
          margin: 0;

          img {
              // width: 80% !important;
              margin-top: 1rem !important;
          }
      }
  }
} ;
